<template>
  <div class="container">
    <h1>Latest Posts</h1>
    <div class="create-post">
      <label for="create-post">New Post: </label>
      <input v-model="text" type="text" id="create-post" />
      <button @click="createPost">Create Post</button>
    </div>
    <p>Double click on a post to delete.</p>
    <p class="error" v-if="error">{{ error }}</p>
    <hr />
    <div class="posts-container">
      <div
        class="post"
        @dblclick="deletePost(post._id)"
        v-for="(post, index) in posts"
        :key="post._id"
        :index="index"
      >
        <p>
          {{ post.createdAt.substr(0, 10) }}
        </p>
        <p class="text">{{ post.text }}</p>
      </div>
    </div>
  </div>
</template>

<script>
import { ref } from "vue";
import PostService from "@/PostService";
export default {
  name: "PostComponent",
  setup() {
    const posts = ref([]);
    const error = ref(null);
    const text = ref("");

    const getPosts = async () => {
      try {
        posts.value = await PostService.getPosts(error.value);
      } catch (err) {
        error.value = err.message;
      }
    };

    getPosts();

    const createPost = async () => {
      // clear any error value
      error.value = "";
      // first check if there is user input
      if (text.value) {
        // thereis user input, so post
        await PostService.insertPost(text.value);
        text.value = "";
        // reload posts
        posts.value = await PostService.getPosts();
      } else {
        error.value = "Please write something before posting!";
      }
    };

    const deletePost = async (id) => {
      await PostService.deletePost(id);
      posts.value = await PostService.getPosts();
    };

    return { posts, error, text, createPost, deletePost };
  },
};
</script>

<style lang="scss" scoped>
.container {
  width: 95%;
  max-width: 500px;
  margin: 0 auto;
  padding-top: 1rem;
  text-align: center;

  .create-post {
    margin-bottom: 1rem;
  }

  .error {
    font-weight: 500;
    color: red;
  }

  hr {
    margin-bottom: 1rem;
  }

  .posts-container {
    .post {
      background-color: #4b4b4b;
      color: white;
      border-radius: 5px;
      margin-bottom: 0.5rem;
      padding: 0.1rem 0rem;
      transition: background-color 0.4s;
      cursor: pointer;

      &:hover {
        background-color: rgb(236, 90, 90);
      }
    }
  }
}
</style>
